.ui-sortable-helper {
    &.nf-field-wrap
    {
        border-style: solid;
        border-color: #fff !important;
        box-shadow: 0px 1px 8px 2px rgba( $dark_color, 0.64 );
        transform: rotate(358deg);
    }
}

.ui-draggable-handle {
    cursor: pointer;
    cursor: grab;
}
.ui-draggable-dragging {
    cursor: grabbing;
    z-index: 99;
    .nf-item {
        background-color: #fff !important;
        border-color: #fff !important;
        border-style: solid !important;
        color: $dark_color !important;
        width: 250px !important;
        &.clicked {
            box-shadow: $shadow !important;
        }
    }
}
#nf-builder {
    .nf-droppable-active {
        border: 4px solid $cta_color;
        transition: all 1.0s ease;
    }
    .nf-droppable-hover {
        border: 4px solid $accent_color;
        transition: all 1.0s ease;
    }
}

.nf-fields-sortable {
    border: 4px solid #FFF;
    padding: 25px 15px 6px;
}
.nf-fields-sortable .hidden {
    display: block !important;
}
.nf-fields-sortable-placeholder { margin-top: -14px; margin-bottom: 6px; border: 4px solid #84CC1E }

.nf-staged-fields-drag {
    position: relative;
    display: block;
    height: 80px;
    &::after {
        clear: both;
        content: '';
        display: block;
    }
    .nf-staged-fields-drag-wrap {
        position: absolute;
        width: 500px;
    }
    #drag-item-1,
    #drag-item-2,
    #drag-item-3 {
        box-shadow: 3px 3px 5px rgba( 0, 0, 0, 0.4 );
    }
    #drag-item-1 {
        bottom: 0px;
        left: 0px;
        z-index: 3;
    }
    #drag-item-2 {
        bottom: 5px;
        left: 5px;
        z-index: 2;
    }
    #drag-item-3 {
        bottom: 10px;
        left: 10px;
        z-index: 1;
    }
}

.nf-staged-fields-drag-wrap {
    background: #fff;
    border: 1px solid #ccc;
    border-radius: 4px;
    color: #888;
    cursor: pointer;
    margin-bottom: 20px;
    padding: 15px 20px;
    &.active {
        border: 2px solid #84CC1E;
        color: #424242;
        font-weight: bold;
        .nf-item-controls {
            font-weight: normal;
            a {
                color: #424242;
            }
            .nf-item-delete,
            .nf-item-duplicate {
                display: none;
            }
            .nf-item-editing {
                display: inline;
            }
            .nf-item-edit {
                .dashicons {
                    margin-right: 12px
                }
            }
        }
    }
}

.nf-staged-fields-sortable-placeholder {
    float: left;
    height: 28px;
    border-right: 8px solid #84CC1E;
    margin-right: -4px;
    margin-left: -4px;
}

.nf-list-options-sortable-placeholder, .nf-listimage-options-sortable-placeholder {
    border: 4px solid #84CC1E;
    height: 8px !important;
    margin: -4px 0 -4px 0;
}

.nf-fields-empty-droppable {
    border: 4px solid #FFF;
    &::after {
        clear: both;
        content: "";
        display: block;
    }
}

.nf-settings .toggle {
    cursor: pointer;
}