.nf-button {
    border: 0;
    border-radius: 4px;
    cursor: pointer;
    display: inline-block;
    font-size: 17px;
    padding: 10px 30px;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    appearance: none;
    &.primary {
        background: $cta_color;
        border: 1px solid #1EA9EA;
        color: #fff;
    }
    &.secondary {
        background: #fff;
        border: 1px solid $cta_color;
        color: $cta_color;
    }
    &.disabled {
        background: #ccc;
        border: 1px solid #ccc;
        cursor: default;
    }
    &.publish {
        float: right;
        border-radius: 100px;
        font-weight: 600;
        letter-spacing: 0.025em;
        padding: 16px 40px;
        .nf-loading {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 70px;
            height: 18px;
            transform: translate(-20px, 15px);
        }
    }
}
.nf-cancel {
    color: #999;
    font-size: 15px;
}
.nf-public-link {
    color: #999;
    font-size: 15px;
}
.nf-mobile-menu {
    background: $cta_color;
    border-radius: 4px;
    color: #fff;
    display: none;
    float: right;
    padding: 5px 7px 5px 5px;
    text-decoration: none;
    transition: margin 0.5s ease;
    .dashicons {
        font-size: 30px;
        width: 32px;
        height: 30px;
    }
    &:hover {
        color: #fff;
    }
}

.ninja-forms-app {
    fieldset {
        border: 2px solid #e7e8ed;
        border-color: rgba(0, 0, 0, .05);
        border-radius: 8px;
        clear: both;
        display: block;
        margin-bottom: 20px;
    }
    label {
        position: relative;
        &::after {
            clear: both;
            content: "";
            display: block;
        }
    }
    legend {
        margin: 0 3px 10px;
        padding: 0 10px;
    }
    .nf-setting-label, label, legend {
        color: #6a7077;
        color: rgba(48, 55, 62, 0.7);
        display: block;
        font-size: 12px;
        font-weight: 600;
        letter-spacing: 0.025em;
        text-transform: uppercase;
        .open-media-manager {
            position: absolute;
            color: #ccc;
            right: 15px;
            top: 5px;
            text-decoration: none;
        }
    }
    input[type=text],
    input[type=number],
    textarea {
        background: #fff;
        border: 1px solid rgba($dark_color, 0.1);
        font-size: 16px;
        font-weight: normal;
        margin-top: 7px;
        padding: 12px 15px;
        width: 100%;
        height: 41px;
        text-transform: none;
    }
    textarea {
        height: 200px;
    }
    .note-editor .note-editing-area {
        border: 1px solid rgba($dark_color, 0.1);
    }
    input[type=checkbox].nf-checkbox {
        appearance: none;
        background: #f9f9f9;
        border: 0;
        height: 25px;
        width: 25px;
        padding: 2px 1px 0px !important;
        margin: -3px -5px;
        &:checked {
            &::before {
                content: "\f147";
                font: 400 30px/1 dashicons;
            }
        }
    }
    // active fields
    input[type=text],
    input[type=number],
    textarea,
    .nf-select select {
        &:focus {
            border-color: rgba($cta_color, 0.5);
            box-shadow: 0 0 2px 0px rgba($cta_color, 0.5);
        }
    }
    .nf-select select {
        &:hover,
        &:focus {
            color: $dark_color;
            & + div {
                border-color: rgba($cta_color, 0.5);
            }
        }
    }
}
.ninja-forms-app {
    .jBox-wrapper.import-options {
        padding: 12px 20px 0px !important;
    }
    .jBox-wrapper.import-options.jBox-pointerPosition-top.jBox-closeButton-box:before {
        right: 10px;
    }
    .nf-list-options, .nf-listimage-options {
        padding-bottom: 20px;
        .nf-div-table {
            .nf-table-row {
                > div {
                    display: table-cell;
                    padding: 5px;
                    &:nth-child(1), &:nth-child(6) {
                        padding: 0 15px;
                        width: 20px;
                    }
                    &:nth-child(2), &:nth-child(3) {
                        width: 45%;
                    }
                    &:nth-child(4) {
                        min-width: 104px;
                    }
                    input {
                        margin: 0;
                    }
                }
                &.nf-table-header {
                    div {
                        font-size: 12px;
                        padding: 20px 5px 5px;
                        &:nth-child(1), &:nth-child(6) {
                            padding: 0 25px;
                        }
                    }
                }
            }
        }
        &.calculations {
            .nf-div-table {
                .nf-table-header{
                    div{
                        &:nth-child(4) {
                            display: none;
                        }
                    }
                }
                .nf-table-row {
                    div {
                        vertical-align: top;
                        position: relative;
                            &:nth-child(1), &:nth-child(4) {
                                padding: 0 15px;
                                width: 20px;
                                min-width: 20px;
                            }
                            &:nth-child(2) {
                                width: 30%;
                            }
                            &:nth-child(3) {
                                width: 70%;
                            }
                        textarea {
                            margin: 0;
                            height: 100px;
                            padding-right: 40px;
                        }
                        .merge-tags {
                            color: #999;
                            position: absolute;
                            right: 15px;
                            top: 5px;
                        }
                        .dashicons {
                            color: #999;
                            margin-top: 10px;
                        }
                    }
                    .calc-left {
                        div {
                            display: inline-block;
                            &:nth-child(1) {
                                width: 100%;
                                padding: 0px;
                            }
                            &:nth-child(2) {
                                width: 60%;
                                text-align: right;
                                font-size: 12px;
                                float: left;
                                padding-top: 20px;
                                padding-right: 10px;
                            }
                            &:nth-child(3) {
                                width: 50px;
                                float: left;
                                padding-top: 10px;
                            }
                        }
                    }
                }
            }
        }
        &.exception_fields,
        &.options,
        &.image_options,
        & > {
            legend:has(.nf-add-new) {
                display: flex;
                align-items: baseline;
                .nf-add-new::before {
                    margin-left: 8px;
                }
            }
        }
    }
    .options {
        position: relative;
        legend {
            .nf-open-import-tooltip {
                background: $light_color;
                border: 2px solid #1EA9EA;
                border-radius: 50px;
                color: $cta_color;
                font-size: 13x;
                padding: 6px 16px;
                text-decoration: none;
                position: absolute;
                right: 15px;
                &:hover {
                    background: $cta_color;
                    color: #fff;
                }
                .fa {
                    margin-right: 4px;
                }
            }
        }
    }
    .button-toggle {
        display: flex;
        justify-content: center;
        text-align: left;
        border: 1px solid #c2c7ce;
        border-color: rgba($dark_color, 0.2);
        border-radius: 100px;
        background: #fff;
        margin-top: 32px;
        padding: 8px;
        width: clamp(300px, 400px, 480px);
        max-width: 100%;
        label {
            width: 50%;
            display: inline-block;
            span {
                width: 100%;
                display: inline-block;
                border-radius: 0;
                font-size: 15px;
                cursor: pointer;
                transition: background-color 0.4s;
            }
            input:checked + span {
                background-color: $cta_color;
                border-color: $cta_color;
                opacity: 1 !important;
            }
            input + span {
                background-color: #8d919f; // rgba($dark_color, 0.55);
                border-color: #8d919f; // rgba($dark_color, 0.55);
                opacity: 0.65;
                &:hover {
                    opacity: 1;
                }
            }
            &:first-child span {
                border-radius: 100px 0 0 100px;
            }
            &:last-child span {
                border-radius: 0 100px 100px 0;
            }
        }
    }
}

// Add-on style overrides
// ----------------------

// Date/Time picker
.ninja-forms-app {
    label[for="restricted_dates"] {
        .flatpickr-day.flatpickr-disabled {
            // disabled dates
            &, &:hover {
                color: #8d919f;
                color: rgba($dark_color, 0.55);
                background: #eaebeb;
                background: rgba($dark_color, 0.1);
                box-shadow: none;
                border-color: #fff;
            }
        }
    }
    .flatpickr-day {
        &.selected,
        &.selected.inRange,
        &.selected:focus,
        &.selected:hover,
        &.selected.prevMonthDay,
        &.selected.nextMonthDay,
        &.startRange,
        &.startRange.inRange,
        &.startRange:focus,
        &.startRange:hover,
        &.startRange.prevMonthDay,
        &.startRange.nextMonthDay,
        &.endRange,
        &.endRange.inRange,
        &.endRange:focus,
        &.endRange:hover,
        &.endRange.prevMonthDay,
        &.endRange.nextMonthDay {
            background: $dark_color;
            border-color: $dark_color;
        }
    }
    span.flatpickr-weekday {
        font-weight: 700;
    }
    .flatpickr-months .flatpickr {
        &-prev-month,
        &-next-month {
            &:hover svg {
                fill: $cta_color;
            }
        }
    }
    .flatpickr-current-month {
        .flatpickr-monthDropdown-months,
        input.cur-year {
            font-weight: 400;
        }
    }
}

// Salesforce field map
.salesforce_field_map .nf-table-row,
.nf-list-options-tbody .nf-table-row {
    & > div {
        vertical-align: middle;
    }
}

// Conditional Logic
.ninja-forms-app {
    .nf-drawer-header {
        .nf-add-new.nf-add-new-condition {
            margin: 12px 0 0 64px
        }
    }
    .nf-condition {
        border: 2px solid #e7e8ed;
        border-color: rgba(0, 0, 0, .05);
        margin-top: 20px;
        border-radius: 8px;
        &.actions {
            .nf-when-container {
                background: transparent;
                padding: 16px;
            }
        }
    }
    .nf-first-when-container,
    .nf-when-container,
    .nf-else-container {
        background: #e7e8ed;
        padding: 16px;
    }
    .nf-then-container {
        padding: 16px;
    }
}

// Export Settings
.ninja-forms-app {
    .nf-list-options.export_settings {
        .nf-table-row.nf-table-header > div {
            display: none;
        }
        .nf-table-row > div {
            display: block;
        }
    }
}

// Multi-part forms (not exactly in the drawer but...)
.ninja-forms-app {
    #nf-mp-drawer {
        background: $light_color;
        ul.ui-sortable {
            margin-top: 8px;
            & > li {
                height: 44px;
                line-height: 40px;
                &.active {
                    border-color: $accent_color;
                    color: $dark_color;
                    font-weight: 600;
                }
            }
        }
    }
    .nf-mp-drawer-scroll {
        &-previous,
        &-next {
            display: flex;
            align-items: center;
            border-radius: 100px;
        }
    }
    .nf-drawer-opened {
        #nf-mp-drawer {
            width: 100%;
        }
    }
}

.nf-drawer-buttons {
    display: none;
    margin: 0 -20px;
    padding: 0;
    li {
        background: #424242;
        border-right: 1px solid #f1f1f1;
        float: left;
        margin: 0;
        padding: 15px;
        text-align: center;
        width: 50%;
        &:last-child {
            border: 0;
        }
        &.nf-drawer-delete {
            color: #dc3232; // red;
        }
        &.nf-drawer-duplicate {
            color: $accent_color;
        }
        .dashicons {
            margin-right: 10px;
        }
    }
    &::after {
        clear: both;
        content: "";
        display: block;
    }
}

.nf-note {
    &.yellow-background-description {
        background: #f7f0c3;
        border-left: 1px solid #ccc;
        border-right: 1px solid #ccc;
        padding: 10px;
        margin-bottom: -25px;

        &.top {
            border-top: 1px solid #ccc;
            border-radius: 4px  4px 0 0;
        }

        &.bottom {
            border-bottom: 1px solid #ccc;
            border-radius: 0 0 4px 4px;
            margin-bottom: 0px;
        }
    }
}

.dashicons.spin {
    animation: dashicons-spin 1s infinite;
    animation-timing-function: linear;
}

@keyframes dashicons-spin {
    0%   { transform: rotate( 0deg ); }
    100% { transform: rotate( 360deg ); }
}