@keyframes beacon_effect {
    0% {
        box-shadow: 0px 0px 0px 0px rgba($cta_color, 0.5);
    }
    25% {
        box-shadow: 0px 0px 0px 16px rgba($cta_color, 0.2);
    }
    100% {
        box-shadow: 0px 0px 16px 28px rgba($cta_color, 0);
    }
}

@keyframes pulse_effect {
    0% {
        transform: scale(.92);
        box-shadow: 0px 0px 0px 0px rgba($cta_color, 0.5);
    }
    25% {
        transform: scale(.96);
        box-shadow: 0px 0px 0px 8px rgba($cta_color, 0.2);
    }
    100% {
        transform: scale(1);
        box-shadow: 0px 0px 8px 14px rgba($cta_color, 0);
    }
}
.nf-item.clicked:not(.ui-draggable-dragging) {
    animation: pulse_effect 0.48s ease-out;
    &.available {
        animation: none; // no click effect on inactive items
    }
}
@media only screen and (max-width: 1400px) {
    #nf-builder.nf-drawer-opened {
        // shadow for curved drawer
        &::before {
            height: 250%; // should be 256 (clip height * 2) but 6% missing???
        }
        #nf-drawer {
            clip-path: ellipse( 64% 128% at 64.1% 50% ); // curve applys on transition
            &.nf-drawer-expand {
                clip-path: ellipse( 100% 128% at 64.1% 50% ); // curve goes away full width
            }
        }
    }
    .nf-settings {
        .nf-field-type-button {
            width: clamp(170px, calc(50% - 24px), 300px);
            max-width: 100%;
        }
    }
}
@media only screen and (max-width: 1280px) {
    .nf-drawer-opened {
        .nf-one-third {
            width: 50%;
        }
        .nf-drawer-expand {
            .nf-one-third {
                width: 33%;
            }
        }
    }
}
@media only screen and (max-width: 1065px) {
    .nf-app-menu {
        .preview {
            .app-menu-text {
                text-indent: -9999px;
                display: inline-block;
                width: 0;
            }
        }
    }
    .nf-drawer-opened {
        /*.nf-one-third {
            width: 50%;
        }*/
        .nf-actions-table {
            th, td {
                &:nth-child(3) {
                    display: none;
                }
            }
        }
        /*.nf-drawer-expand {
            .nf-one-third {
                width: 33%;
            }
        }*/
    }
}
@media only screen and (max-width: 900px) {
    #nf-header {
        ul {
            display: none;
        }
        .nf-button.primary {
            display: none;
        }
    }
    .nf-mobile-menu {
        display: inline-block;
    }
    #nf-app-sub-header {
        h2 {
            padding-left: 0;
            transition: all 1.0s ease 0s
        }
    }
    .nf-add-new.nf-open-drawer {
        position: fixed;
        text-indent: -9999px;
        height: 54px;
        width: 54px;
        top: auto;
        left: auto;
        bottom: 20px;
        right: 20px;
        &:after {
            background: $accent_color;
            border: 2px solid $accent_color;
            border-radius: 40px;
            color: #fff;
            content: "+";
            font-size: 50px;
            padding: 8px;
            position: absolute;
            left: 0;
            width: 28px;
            height: 28px;
            line-height: 24px;
            text-indent: 0px;
        }
    }
    #nf-main-header {
        .nf-button {
            display: none;
        }
    }
    .nf-one-half, .nf-one-third {
        //float: none;
        width: 100%;
    }
    .nf-drawer-expand {
        .nf-one-half {
            float: left;
            width: 50%;
        }
        .nf-one-third {
            float: left;
            width: 33%;
        }
    }
    #nf-app-form-title {
        margin-top: -55px;
    }
    #nf-main {
        top: 160px;
    }

    #nf-table-display {
        .nf-item-controls {
            width: auto;
        }
    }
}
@media only screen and (max-width: 1023px) {
    .ninja-forms-app {
        &.folded {
            #wpcontent {
                margin-left: 36px;
                padding: 0;
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                #wpbody {
                    left: 0;
                }
            }
        }
    }
    .nf-setting-wrap {
        width: 50%;
    }
    .nf-drawer-opened {
        #nf-main {
            width: 100%;
        }
        .nf-setting-wrap {
            width: 50%;
        }
        &#nf-builder {
            #nf-drawer {
                clip-path: ellipse( 100% 128% at 64.1% 50% ); // curve goes away full width
                width: 100%;
                right: 0;
                transition: all 0.5s ease;
                #nf-drawer-header {
                    width: 100%;
                }
            }
            .nf-toggle-drawer {
                display: none;
            }
        }
    }
    .nf-drawer-closed {
        .nf-setting-wrap {
            width: 50%;
        }
    }
}
@media only screen and (max-width: 960px) {
    .ninja-forms-app {
        &.auto-fold {
            #wpcontent {
                margin-left: 36px;
                padding: 0;
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                #wpbody {
                    left: 0;
                }
            }
        }
    }
    .nf-drawer-opened .nf-toggle-drawer {
        display: none;
    }
}
@media only screen and (max-width: 782px) {
    .ninja-forms-app {
        &.folded, &.auto-fold {
            #wpcontent {
                margin-left: 0;
            }
        }
    }
    #nf-builder {
        padding: 80px 20px 10px;
        .nf-cta-bubble {
            display: none !important;
        }
    }
    #nf-main {
        padding: 4px 0 80px 16px;
    }
    #nf-drawer {
        padding-left: 20px;
        padding-right: 20px;
    }
    .nf-search {
        &::before {
            left: 24px;
        }
        input[type=search] {
            padding-left: 60px;
        }
    }
    #nf-main-content {
        width: 100%;
        max-width: 100%;
    }
    #nf-main {
        border: 0;
    }
}
@media only screen and (max-width: 660px) {
    .nf-one-half, .nf-one-third {
        width: 100%;
        float: none;
    }
    .nf-actions-table {
        td:nth-child(3),th:nth-child(3) {
            display: none;
        }
    }
    .nf-drawer-buttons {
        display: block;
    }
    .nf-item {
        padding-top: 12px;
        padding-bottom: 12px;
        .fa {
            padding-top: 12px;
            padding-bottom: 12px;
            width: 48px;
        }
    }
    .nf-saved .nf-item::after {
        padding: 14px;
    }
}
