.nf-add-new {
    color: $cta_color;
    display: inline-block;
    padding: 1px;
    text-decoration: none;
    position: relative;
    height: 32px;
    line-height: 32px;
    z-index: 98;
    overflow: hidden;
    &::before {
        background: $cta_color;
        border: 2px solid $cta_color;
        border-radius: 100px;
        color: #fff;
        content: "\f543";
        font-family: 'Dashicons';
        font-size: 16px;
        padding: 2px;
        margin: 0 4px 0 0;
        width: 17px;
        height: 17px;
        line-height: 17px;
        text-indent: 0px;
        text-align: center;
        display: inline-block;
        transform: translateY(2px);
        transition: all 0.5s ease 0s
    }
    &:hover, &:active, &.active {
        color: $cta_color;
        transition: all 0.5s ease;
        &::before {
            background: #fff;
            color: $cta_color;
            transform: rotate(180deg) translateY(-2px);
            transition: all 0.5s ease;
        }
    }
    &:focus {
        box-shadow: none;
    }
    &.nf-open-drawer {
        position: absolute;
        top: 185px;
        left: 40px;
    }
}
.nf-add-new.disabled {
    display: none;
}
.ninja-forms-app {
    &.folded {
        .nf-add-new {
        }
    }
}

.nf-master-control {
    background: $cta_color;
    border: 4px solid $cta_color;
    border-radius: 100px;
    cursor: pointer;
    position: absolute;
    height: 64px;
    width: 64px;
    top: auto;
    left: auto;
    bottom: 32px;
    right: 40px;
    transition: all 0.5s ease;
    z-index: 98;
    span {
        display: block;
        text-indent: -9999px;
    }
    // pseudo element for onload animation effect
    &::before {
        content: '';
        animation: beacon_effect 1.75s ease-out 2s 3 alternate;
        border-radius: 100px;
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 0;
    }
    &:hover {
        animation: beacon_effect 1.25s ease-out 50ms;
    }
    &:hover, &:active, &.active {
        background: #fff;
        color: $cta_color;
        transform: rotate(180deg);
        transition: all 0.5s ease;
    }
}
.nf-open-drawer,
#nf-main-gutter-right {
    .nf-plus {        
        width: 28px;
        stroke-width: 12px;
        stroke: #fff;
        pointer-events: none; // force clicks to bubble up
        }
    &:link {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &:hover, &:active, &.active {
        .nf-plus {
            stroke: $cta_color;

            // FUTURE: transform + (plus) into → (arrow) on hover
