            // }
            // &__angle--top {
            //     rotate: -45deg;
            // }
            // &__angle--bottom {
            //     rotate: 45deg;
            // }
        }
    }
    &.nf-secondary-control {
        .nf-plus {
            position: static;
            background: $cta_color;
            border: 3px solid $cta_color;
            border-radius: 100px;
            margin-right: 8px;
            padding: 6px;
            stroke-width: 20px;
            transform: none;
            transition: all 0.5s ease;
            width: 32px;
        }
        &:hover, &:active, &.active {
            .nf-plus {
                background: #fff;
                color: $cta_color;
                transform: rotate(180deg);
                transition: all 0.5s ease;
            }
        }
    }
}

.nf-secondary-control {
    color: #25a8e8;
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 20px;
    text-decoration: none;
    box-shadow: none;
    transition: all 0.5s ease;
    z-index: 98;
    .fa {
        background: #25a8e8;
        border-radius: 50px;
        color: #fff;
        cursor: pointer;
        display: inline-block;
        height: 30px;
        margin: 0 8px 0 0;
        padding: 8px;
        transition: all 0.5s ease;
        width: 30px;
    }
    &:hover,
    &:active,
    &.active {
        background: #fff;
        color: $cta_color;
        transition: all 0.5s ease;
        .fa {
            color: #fff;
            transform: rotate(180deg);
            transition: all 0.5s ease;
        }
    }
    &:focus {
        box-shadow: none;
    }
}
