#nf-drawer {
    background: $light_color;
    background: linear-gradient(32deg, rgba(246,247,251,1) 0%, rgba(236,239,247,1) 100%);
    border-radius: 0;
    padding: 0px 40px 0px 64px;
    width: calc($drawer_width * 1%);
    clip-path: ellipse( 100% 128% at 64.1% 50% ); // starts without curve
    position: absolute;
    top: 0;
    bottom: 0;
    right: -1400px;
    overflow-y: scroll;
    overflow-x: hidden;
    z-index: 102;
    -webkit-overflow-scrolling: touch;
}
#nf-builder {
    // shadow for curved drawer
    &::before {
        content: "";
        display: block;
        position: absolute;
        transform: translateY(-50%); // for vertical center
        top: 50%; // for vertical center w/ translateY 50%
        right: calc(-1% * calc(64.1 - 52) - 1400px ); // drawer clip position - drawer width - drawer start position
        width: 64%; // match clip width
        height: 282%; // should be 288 (clip height * 2) but 6% missing???
        background: rgba( $dark_color, 0 );
        border-radius: 0%;
        box-shadow: 2px 0px 9px rgba( $dark_color, 0 );
        transition: all 0.4s ease;
        z-index: 101;
    }
}
#nf-builder.nf-drawer-opened {
    .nf-main-test {
        width: 50%;
        transition: all 0.5s ease;
        .nf-button {
            display: none;
        }
    }
    // shadow for curved drawer
    &::before {
        background: rgba( $dark_color, 0.24 );
        border-radius: 100%;
        box-shadow: -2px 0px 18px rgba( $dark_color, 0.28 );
        right: calc(-1%* calc(64.1 - $drawer_width)); // drawer clip position - drawer width
        transition: all 0.5s ease 0.0125s;
    }
    #nf-drawer {
        clip-path: ellipse( 64% 144% at 64.1% 50% ); // curve applys on transition
        right: 0;
        transition: all 0.5s ease;
        a.nf-external-info {
            background-image: url('../img/help_icon.png');
            background-size: 20px;
            width: 20px;
            height: 20px;
            display: inline-block;
            margin-left: 2px;
        }
        &.nf-drawer-expand {
            clip-path: ellipse( 100% 144% at 64.1% 50% ); // curve goes away full width
            width: 100%;
            transition: all 0.5s cubic-bezier(0.18, 0.89, 0.32, 1.28);
            .nf-toggle-drawer {
                .dashicons {
                    transform: rotate(180deg);
                    transition: all 0.5s ease;
                }
                .nf-expand-off {
                    display: none;
                }
                .nf-expand-on {
                    display: inline;
                }
            }
            #nf-drawer-header {
                width: 100%; // for sticky position, use: calc(100% + 64px + 40px);
                transition: all 0.5s cubic-bezier(0.18, 0.89, 0.32, 1.28);
            }
        }
        #nf-drawer-header {
            right: 0;
        }
    }
    .preview {
        opacity: 0.0;
        visibility:hidden;
        filter: alpha(opacity=0);
        transition: all 0.50s ease;
        &:hover, &:active, &.active {
            opacity: 0.0;
            visibility:hidden;
            filter: alpha(opacity=0);
            transition: all 0.50s ease;
        }
    }
    #nf-main-content,
    #nf-main-header {
        margin: 0 auto;
        transition: all 0.50s ease;
    }
    @media only screen and (min-width: 901px) {
        #nf-app-sub-header {
            .nf-add-new {
                top: 139px !important;
                text-indent: -99999px;
            }
        }
    }
    .nf-setting-wrap {
        width: 50%;
        transition: width 0.50s ease;
    }
    .nf-toggle-drawer {
        display: block;
    }
}
.nf-drawer-closed {
    #nf-main {
        width: 100%;
        left: 0;
        transition: width 0.50s ease, left 0.50s ease;
    }
    #nf-drawer {
        right: -1400px;
        transition: right 0.50s ease;
    }
    .nf-add-new {
        opacity: 1.0;
        visibility: visible;
        filter: alpha(opacity=100);
        transition: all 0.50s ease;
    }
    .nf-setting-wrap {
        width: 33%;
        transition: width 0.50s ease;
    }
    .nf-toggle-drawer {
        display: none;
    }
}
#nf-drawer {
    #nf-drawer-header {
        position: fixed; // sticky;
        width: calc($drawer_width * 1%); // calc(100% + 64px + 40px);
        top: 0;
        right: -1400px;

        background: #fff;
        background: linear-gradient(212deg, rgb(246, 247, 251) 0%, #fff 100%);
        height: 60px;;
        margin-left: -64px;
        transition: width 0.50s ease, right 0.5s ease;
        z-index: 101;
        .nf-drawer-header {
            .nf-button {
                // Docked Inline Button
                // --------------------
                // border-radius: 0;
                // float: right;
                // padding: 20px 48px;

                // Circular Checkmark Button
                // -------------------------
                $chk_btn_size: 56px; // +4px border = 64px
                box-sizing: content-box;
                position: absolute;
                right: 40px;
                bottom: 0;
                width: $chk_btn_size;
                height: $chk_btn_size;
                border-radius: 100px;
                border: 4px solid $cta_color;
                background: $cta_color;
                color: $cta_color;
                font-size: 0px !important;
                line-height: 0 !important;
                padding: 0 !important;
                transform: translateY(50%) rotate(-45deg);
                // animation: beacon_effect 2s ease-out 2s;
                &::before {
                    border: 4px solid #fff;
                    border-width: 0 0 4px 4px;
                    content: '';
                    display: block;
                    position: relative;
                    width: 40%; // calc(.4 * $chk_btn_size);
                    height: 16%; // calc(.16 * $chk_btn_size);
                    transform-origin: 100% 0;
                    transform: translate(60%, 160%);
                    z-index: 1;
                }
                &::after {
                    content: '';
                    position: relative;
                    top: -4px;
                    right: -4px;
                    display: block;
                    width: 40%; // calc(.4 * $chk_btn_size);
                    height: 16%; // calc(.16 * $chk_btn_size);
                    border: 4px solid #fff;
                    border-width: 4px 0 0 0;
                    transform-origin: 100% 0;
                    transform: translate(60%, 160%);
                }
                &, &::before, &::after {
                    transition: all 0.35s ease;
                }
                &:hover {
                    animation: beacon_effect 1.25s ease-out 50ms;
                    background: #fff;
                    transform: translateY(50%) rotate(225deg);
                }
                &:hover::before {
                    width: 25%; // calc(.25 * $chk_btn_size);
                    height: 25%; // calc(.25 * $chk_btn_size);
                    border-color: $cta_color;
                    transform: translate(85%, 120%);
                    transition: all 0.25s ease;
                }
                &:hover::after {
                    top: -8px;
                    width: 55%; // calc(.55 * $chk_btn_size);
                    border-color: $cta_color;
                    transform: rotate(-45deg) translate(0%, 40%);
                }
            }
        }
    }
    #nf-drawer-content > div:first-child {
        padding: 72px 0 64px;
        .nf-setting-title:first-child {
            display: block;
        }
        > .nf-actions-itmes-installed {
            padding-top: 24px;
        }
    }
}
.nf-drawer-error {
    background-color: #FFBABA;
    border: 1px solid #FFBABA;
    border-radius: 5px;
    color: #D8000C;
    font-size: 12px;
    margin: 10px 0px;
    padding: 10px 10px;
}
.nf-settings {
    display: flex;
    flex-flow: row wrap;
    gap: 8px 24px;
    margin: 0 0 24px;
    h3 {
        color: $dark_color;
        font-size: 14px;
        margin: 4px 0;
        padding: 0 8px;
        text-transform: uppercase;
        width: 100%;
        .dashicons {
            color: #c2c6cc;
            color: rgba( $dark_color, 0.24 );
            font-size: 20px;
            margin: -1px 8px 0 -12px;
            &-arrow {
                &-down::before {
                    content: "\f347"; // override arrow type
                }
                &-right::before {
                    content: "\f345"; // override arrow type
                }
            }
        }
    }
    .nf-field-type-button {
        width: clamp(170px, calc(33.33% - 24px), 300px);
        max-width: 100%;
    }
    .nf-field-settings {
        width: 800px;
        max-width: 100%;
    }
}
.nf-full {
    clear: both;
    &.toggle-row {
        border-bottom: 1px solid #ccc;
        margin: 0;
        position: relative;
        &:first-child {
            border-top: 1px solid #ccc;
        }
        label {
            display: inline-block;
            padding: 15px 60px 15px 0;
        }
        .nf-toggle {
            float: right;
            margin: 15px 0;
            position: absolute;
            top: 0;
            right: 10px;
        }
    }
    .nf-select{
        select{
            display: block;
            max-width: 100%;
        }
    }
}
.nf-one-half,
.nf-one-third {
    float: left;
}
.nf-one-half {
    width: 50%;
}
.nf-one-third {
    width: 33.33%;
}
.nf-first {
    clear: both;
    margin-left: 0;
}
.nf-full,
.nf-one-half,
.nf-one-third {
    margin-bottom: 15px;
    padding: 0 2% 0 0;
    fieldset & {
        padding-left: 2%;
    }
}
.nf-full::after,
.nf-one-half::after,
.nf-one-third::after {
    clear: both;
    content: "";
    display: block;
}
.nf-toggle-drawer {
    color: #aaa;
    cursor: pointer;
    display: none;
    float: right;
    margin-top: 40px;
    text-decoration: none;
    padding: 10px 20px;
    position: fixed;
    bottom: 0;
    right: 0;
    .dashicons {
        margin-right: 5px;
        transition: all 0.50s ease;
    }
    .nf-expand-off {
        display: inline;
    }
    .nf-expand-on {
        display: none;
    }
}


#nf-menu-drawer {
    background: $light_color;
    box-shadow: inset 1px 4px 9px -6px;
    width: 250px;
    position: absolute;
    top: 0;
    bottom: 0;
    right: -250px;
    z-index: 999;
    transition: right 0.50s ease;
    ul {
        margin: 0;
        li {
            a {
                border-bottom: 1px solid #ccc;
                color: #424242;
                display: block;
                padding: 10px 20px;
                text-decoration: none;
                .dashicons {
                    color: $cta_color;
                    margin-right: 15px;
                }
            }
        }
    }
    .nf-publish {
        background: $cta_color;
        color: #fff;
        padding: 20px;
        text-align: center;
        text-transform: uppercase;
        cursor:pointer;

        &.disabled {
            background: #ccc;
            border: 1px solid #ccc;
            cursor: default;
        }
    }

}

.nf-menu-expand {
    #nf-menu-drawer {
        right: 0;
        transition: right 0.50s ease;
    }
    #nf-header {
        margin-right: 250px;
        margin-left: -250px;
        transition: margin 0.50s ease;
    }
    #nf-main {
        left: -250px;
        transition: left 0.50s ease;
    }
}

.nf-div-table {
    .nf-table-row {
        &.nf-error {
            textarea {
                border: 1px solid red;
            }
            span.nf-option-error {
                background: red;
                color: #fff;
                display: block;
                padding: 5px 0;
                width: 100%;
                div.nf-error {
                    display: block;
                    width: 100% !important;
                    text-align: center;
                }
            }
        }
    }
}
.ninja-forms-app {
    .jBox-Notice {
        .jBox-container {
            background: $accent_color;
            font-size: 18px;
        }
    }
}

.available-action-modal {
  padding: 20px;
  img {
    width: 100%;
  }
  iframe {
    width: 100%;
    height: 214px;
  }
  p:first-of-type {
    font-size: 16px;
    font-weight: bold;
    line-height: 20px;
    padding: 16px 40px;
    text-align: center;
  }
  .actions {
    margin-top: 30px;
    &::after {
      display: block;
      clear: both;
      content: "";
    }
  }
  .primary.nf-button {
    float: right;
  }
  ul {
    list-style-type: disc;
    li {
        margin-left: 20px;
        padding-left: 10px;
    }
  }
}