.ninja-forms-app {
    .nf-select {
        display: block;
        position: relative;
        select {
            background: transparent;
            border: 0;
            margin-top: 7px;
            height: 41px !important;
            padding: 0 30px 0 15px;
            position: relative;
            width: 100%;
            font-size: 16px;
            font-weight: normal;
            text-transform: capitalize;
            appearance: none;
            z-index: 2
        }
        div {
            background: #fff;
            border: 1px solid rgba($dark_color, 0.1);
            border-radius: 4px;
            height: 41px;
            position: absolute;
            bottom: 0;
            width: 100%;
            right: -1px;
            z-index: 1;
        }
        div::after {
            color: #8d919f;
            color: rgba($dark_color, 0.55);
            content: "\f347";
            font-family: 'Dashicons';
            font-size: 18px;
            position: absolute;
            right: 12px;
            bottom: 0;
            transform: translateY(-50%);
        }
    }
    div.nf-select {
        div {
            bottom: 7px;
            width: 95%;
        }
        select {
            margin-top: 0;
        }
    }
    .autobuild-template-select .nf-select select {
        background: #fff url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2220%22%20height%3D%2220%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22M5%206l5%205%205-5%202%201-7%207-7-7%202-1z%22%20fill%3D%22%23555%22%2F%3E%3C%2Fsvg%3E') no-repeat right 5px top 55%;
        border: 1px solid rgba($dark_color, 0.1);
        border-radius: 4px;
    }
}
