.nf-search {
    display: inline-block;
    position: relative;
    width: 65%;
    width: clamp( 200px, 65%, 600px);
    input[type=search] {
        background: transparent;
        box-shadow: 0 0 0;
        border: 0;
        font-size: 16px;
        height: 60px;
        padding: 16px 0 16px 104px;
        width: 100%;
        &::placeholder {
            color: #8d919f;
            color: rgba( $dark_color, 0.55 );
        }
    }
    &::before {
        content: "\f179";
        color: rgba( $dark_color, 0.24 );
        font-family: "Dashicons";
        font-size: 30px;
        width: 30px;
        height: 30px;
        line-height: 30px;
        position: absolute;
        top: 1px;
        left: 68px;
    }
}
.nf-stage {
    position: relative;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    .nf-stage-controls {
        background: #F8F8F9;
        padding: 5px;
        position: absolute;
        top: -20px;
        right: 10px;
    }
}
.nf-reservoir {
    &::after {
        clear: both;
        content: "";
        display: block;
    }
    &.nf-droppable-active,
    &.nf-staged-fields-active {
        background: #F8F8F9;
        border: 4px solid #F8F8F9;
        border-radius: 4px;
        margin: 30px 2% 20px;
        min-height: 52px;
        padding: 6px;
        transition: all 1.0s ease;
    }
    .nf-item-dock {
        background: #fff;
        border-radius: 4px;
        color: #999;
        display: inline-block;
        float: left;
        margin: 0 4px 3px 4px;
        padding: 5px 10px;
        position: relative;
        .dashicons {
            font-size: 14px;
            margin: 2px 0 -4px 5px;
        }
        .fa {
            margin-right: 10px;
        }
    }
}
.nf-item {
    background: transparent;
    background-color: rgb(255 255 255 / 0%);
    border: 2px dashed transparent;
    border-radius: 8px;
    color: $dark_color;
    font-size: 16px;
    padding: 8px 8px 8px 40px;
    position: relative;
    transition: all 100ms ease-in-out 25ms;
    &:hover {
        background-color: rgb(255 255 255 / 32%);
        border: 2px dashed #c2c7ce;
        border-color: rgba($dark_color, 0.2);
    }
    .fa {
        background: transparent;
        border-top-left-radius: 8px;
        border-bottom-left-radius: 8px;
        padding: 8px 0 8px 12px;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        width: 34px;
        line-height: 20px;
    }
}
.nf-saved {
    .nf-item {
        position: relative;
    }
}
.nf-action-items {
    .nf-item {
        border-style: solid;
        border-color: $dark_color;
        padding: 12px 8px;
        text-align: center;
        cursor: pointer;
        &:hover {
            background-color: $dark_color;
            color: #fff;
        }
        &.nf-has-img {
            background-position: center center;
            text-indent: -99999px;
        }
    }
    &.available {
        .nf-item {
            &.nf-has-img {
                filter: grayscale(100%);
                opacity: 0.6;
                &:hover {
                    filter: grayscale(0);
                    opacity: 1.0;
                }
            }
        }
    }
    // can't drag these but JS is allowing so hide dragged item
    .ui-draggable-dragging {
        display: none !important;
    }
}
.nf-actions-itmes-installed,
.nf-actions-items-available {
    .action-types {
        width: 100%;
    }
}

.nf-field-type-button.available,
.nf-actions-items-available {
    .nf-item {
        &, 
        &:link, 
        &:hover, 
        &:active, 
        &:focus {
            cursor: pointer;
            cursor: help;
            color: rgba($dark_color, .55);
            background: rgba($dark_color, .06);
            border-color: rgba($dark_color, 0);
            user-select: none;
        }
    }
}
.nf-actions-items-available {
    .nf-item {
        &, 
        &:link, 
        &:hover, 
        &:active, 
        &:focus {
            cursor: alias;
        }
    }
}
