.nf-realistic-field {
    font-size: 18px!important;
}

.nf-field-label {
    overflow: visible!important;
}

.nf-realistic-field {
    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="number"],
    select,
    textarea {
        width: 100%;
        max-width: 100%;
        height: 50px !important;
        background: rgba($light_color, 0.5) !important;
        border: 1px solid #8d919f !important;
        border-color: rgba($dark_color, 0.55) !important;
        color: $dark_color !important;
        padding: 12px;
    }
    textarea {
        height: 160px !important;
    }
    select[multiple] {
        color: $dark_color !important; 
        height:auto!important;
    }
    input[type="checkbox"],
    input[type="radio"] {
        height: 16px;
        width: 16px;
    }
    .nf-realistic-field--element {
        input[type="checkbox"],
        input[type="radio"] {
            border-width: 2px !important;
            background: $light_color;
            box-shadow: none;
        }
    }
    ul {
        margin: 0;
        list-style-type: none;
        li {
            display: flex;
            margin-bottom: 12px;
            input[type="checkbox"],
            input[type="radio"] {
                padding: 12px;
                margin-top: 4px;
                margin-right: 8px;
                // Faux-label element used in the builder.
                & + div { 
                    margin-top: 3px; // Align the faux-label vertically.
                }
            }
        }
    }
    // star rating
    .fa-star {
        color: #FB0;
    }
    // buttons
    button,
    input[type="button"],
    input[type="submit"] {
        width: auto;
        height: 50px;
        padding: 12px 24px;
        border-radius: 4px;
        background: $dark_color; // #333;
        border: 0;
        color: #fff; // #f7f7f7;
        font-size: 16px;
        font-weight: 600; // 700
        text-transform: uppercase;
    }
    // labels
    .nf-realistic-field--label {
        font-size: 16px !important;
        margin-bottom: 8px !important;
        text-transform: none !important;
        .nf-field-label {
            font-weight: 600; //bold;
            margin-left: 0 !important;
        }
    }
}

.nf-field-wrap {
    .nf-placeholder-label {
        .fa.fa-square-o {
            // same as realistic field for checkbox (in section above)
            display: inline-block;
            width: 16px;
            height: 16px;
            border: 2px solid #8d919f !important;
            border-radius: 4px;
            background: $light_color;
            box-shadow: none;
            position: relative;
            top: 2px;
            &::before {
                display: none;
            }
            & + .nf-field-label {
                margin-left: 12px;
            }
        }
    }
}

.ninja-forms-req-symbol {
    color: #dc3232; // red;
}
.nf-field-wrap {
    background: #fff;
    border: 2px dashed #c2c7ce;
    border-color: transparent;
    border-radius: 8px;
    color: #888;
    cursor: pointer;
    margin-bottom: 20px;
    padding: 15px 20px;
    position: relative;
    transition: border-color .25s;
    &:hover {
        border-color: #c2c7ce;
        border-color: rgba($dark_color, 0.2);
    }
    & .nf-item-controls {
        display: none;
    }
    &:hover .nf-item-controls {
        display: block;
    }
    &.active {
        border: 2px solid #84CC1E!important;
        color: #424242;
        .nf-item-controls {
            font-weight: normal;
            a {
                background: #fff;
                color: $accent_color;
                &:hover {
                    border-color: #fff;
                }
            }
            .nf-item-delete,
            .nf-item-duplicate {
                display: none;
            }
            .nf-item-editing {
                display: inline;
            }
            .nf-item-edit {
                .dashicons {
                    margin-right: 12px
                }
            }
        }
    }
    &.note {
        background: #ffffee;
    }
}

.nf-group-wrap {
    background: #fff;
    border: 2px solid #ccc;
    border-radius: 4px;
    color: #888;
    margin: 0 -10px 20px;
    padding: 15px 30px;
    position: relative;
    .nf-item-controls {
        width: 128px;
    }
    .nf-item-expand {
        display: inline !important;
    }
    .nf-field-wrap {
        margin-left: -20px;
        margin-right: -20px;
        visibility: hidden;
        opacity: 0.0;
        filter: alpha(opacity=0);
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
    }
    &.expanded {
        border-color: $accent_color;
        color: $accent_color;
        .nf-item-expand {
            .dashicons {
                transform: rotate(90deg);
            }
        }
        .nf-field-wrap {
            opacity: 1.0;
            visibility: visible;
            filter: alpha(opacity=100);
            position: relative;
            top: 0;
            left: 0;
            right: 0;
            transition: all 1.0s ease;
            &:nth-child(2) {
                margin-top: 20px;
            }
            &:last-child {
                margin-bottom: 0;
            }
            .nf-item-controls {
                width: 96px;
                .nf-item-expand {
                    display: none !important;
                }
            }
        }
    }
}
