.nf-item-controls {
    z-index: 2;
}

.nf-realistic-field {
    color: black !important;
}

.ninja-forms-req-symbol {
    color: #dc3232; // red;
}

.listselect,
.listcountry,
.liststate {
    .nf-realistic-field .nf-realistic-field--label {
        margin-bottom: 8px;
    }
    .nf-realistic-field .nf-realistic-field--element select {
        border: 1px solid #c4c4c4;
        box-shadow: none;
        appearance: none;
        margin-top: 7px;
    }
    .nf-realistic-field .nf-realistic-field--element > div {
        position: relative;
    }
    .nf-realistic-field .nf-realistic-field--element select + div {
        display: block;
        position: absolute;
        font-size: 16px;
        line-height: 16px;
        margin: 0;
        margin-top: 7px;
        padding: 16px;
        height: 50px;
        top: 0;
        right: 0;
        left: 0;
        z-index: 1;
    }
    .nf-realistic-field .nf-realistic-field--element select + div::after {
        content: "\f078";
        font-family: FontAwesome;
        font-size: 20px;
        position: absolute;
        right: 10px;
        bottom: 0;
        height: 50px;
        line-height: 50px;
    }
}

.starrating .nf-realistic-field--element {
    margin-top: 10px;
}

.checkbox .nf-realistic-field input:checked {
    margin: 4px 5px 1px 0;
}
.checkbox .nf-realistic-field input:checked::before {
    content: "\f00c";
    font-family: FontAwesome;
    font-size: 24px;
    position: absolute;
    top: 20px;
    left: 25px;
    z-index: 2;
    color: black;
}

.listcheckbox .nf-realistic-field li {
    position: relative;
}
.listcheckbox .nf-realistic-field input:checked::before {
    content: "\f00c";
    font-family: FontAwesome;
    font-size: 24px;
    position: absolute;
    left: 5px;
    z-index: 2;
    color: black;
}

.listradio .nf-realistic-field li {
    position: relative;
}
.listradio .nf-realistic-field input:checked {
    border-color: black !important;
}
.listradio .nf-realistic-field input:checked::before {
    border-radius: 50%;
    content: "";
    height: 10px;
    width: 10px;
    position: absolute;
    left: 1px;
    top: 5px;
    z-index: 2;
    background-color: black;
}

@import "label-position/above";
@import "label-position/below";
@import "label-position/hidden";
@import "label-position/left";
@import "label-position/right";