/*
WP Color Picker (Iris) Styles
---------------------------------------------*/
.ninja-forms-app {
    #wpcontent .wp-picker-holder {
        position: absolute;
        z-index: 9001;
        * {
            box-sizing: content-box;
        }
    }
    .wp-picker-container {
            margin-top: 7px;
            width: 100%;
        .wp-color-result {
            background: #fff;
            border: 0;
            box-shadow: none;
            margin: 0;
            height: 41px;
            width: 100%;
            padding-left: 60px;
            &::after {
                font-size: 16px;
                line-height: 41px;
            }
            &.button {
                .wp-color-result-text {
                    background: #fff;
                    color: $dark_color;
                    margin: 0 1px 0 0;
                    height: 39px;
                    line-height: 39px;
                }
            }
            &.wp-picker-open {
                display: none;
            }
        }
        .wp-picker-input-wrap {
            width: 100% !important;
            > .button.wp-picker-default {
                display: none;
            }
            .wp-color-picker {
                width: 100% !important;
            }
        }
    }
}